

.global_limit{
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.61);
    width: 100vw;
    height: 100vh;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
}


.limit{
    position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
    padding: 20px;
  background: red;
  color: wheat;
}
.button_close{
    position: absolute;
    top: 0;
    right: 0;
}
.input_limit{
    margin-top: 10px;
    text-align: center;
    width: 100%;
}