
.main_conteiner_profile{
    position: fixed;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.61);
    height: 100vh;
    width: 100vw;
    z-index: 9;
}
.conteiner_profile{
    background: rgb(187, 189, 187);
    position: relative;
    max-width: 350px;
    width: 80%;
}
.conteiner_profile_logNu{
    display: flex;
    font-size: 20px;
    color: rgb(58, 58, 58);

}


.conteiner_profile_logNu img{
    height: 80px;
    width: 80px;
}
.num_web{
    display: flex;
    flex-direction: column;
    margin-left: 5px;
    font-size: 15px;
}
.newFail_button{
    position: absolute;
    top: 0;
    right: 0;
    background: red;
    font-size: 20px;
    z-index: 10;
}

.conteiner_profile_description{
    margin-top: 10px;
    padding: 0 10px;
    height: 150px;
    overflow: scroll;
    font-size: 17px;
}

