.global_window{
    position: fixed;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.61);
    height: 100vh;
    width: 100vw;
    
   
}



.plus_window{
    
    position: relative;
    max-width: 350px;
    height: 200px;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    background: rgb(94, 72, 72);
    border: solid 2px rgb(250, 249, 249);
}
.close{
    position: absolute;
    top: 0;
    right: 0;
}
.close:hover{
    background: red;
    cursor: pointer;
}