


.company{
    display: flex;
    height: 100vh;
    width: 100vw;

  
}
.container_data_global{
    width: 150px;


    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
   
   
   
    
    
}
.container_data_global div {
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.2vh solid black;

}
div.img_company{
height: 25vh;
background-color: royalblue;
}

div.name_company{
    height: 15vh;
    background-color: seagreen;

}

.symbol_company{
height: 10vh;
background-color: sienna;
}

.price_global{
    height: 10vh;
    background-color: silver;

}


.chages_company{
    height: 10vh;
    background-color: rgb(4, 88, 128);
}

.chagesPercent_global{
    height: 10vh;

}

div.ceo_company{
height: 18.5vh;
background-color: skyblue;
}


.back{
    position: absolute;
    top: 0;
    left: 0;
}

.container_data_all{
    display: flex;
   overflow: scroll;
}
.information_company{
    position: relative;
     display: flex;
     flex-direction: column;
     height: 100vh;
     width: 155px;
    
 }
 
 .information_company div{
    display: flex;
    justify-content: center;
    align-items: center;
     border: 0.2vh solid black;
 }
.img_comp{
    height: 25vh;
}
.img_comp img{
    height: 100%;
    max-width: 150px;
}



.close{
    position: absolute;
    background: red;
    cursor: pointer;
    top: 0;
    right: 0;
}
