


.current_result{
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    max-width: 600px;
    /* z-index: -2; */
    overflow: scroll;
    height: 100px;
   

   padding: 0  0 0 0;
   
}




.current_actions{
   min-height: 80px;
   width: 96%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* z-index: 2; */
    border-bottom: solid 1px black;
    margin: 10px 0;
    max-width: 1000px;
   
}
.current_actions:hover{
    box-shadow: 0px 1px 10px rgb(163, 162, 162);
}


.company_name{
    font-size: 17px;
    max-width: 117px;
    
  
}


.prices{
    font-family: 'Bungee Inline', cursive;
}
.img_pr{
    width: 50px;
}
.button_company{
    background: rgb(109, 109, 214);
    color: white;
    cursor: pointer;
}
.button_add{
    margin-left: 2px;
}
@media screen and (max-width: 700px) {
    .company_name{
        font-size: 11.5px;
       
        
      
    }
}