

.stock_line_container{
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    white-space: nowrap;
    overflow: scroll;
    background: #f1f1f1;
    box-shadow: 0px 0px 10px rgb(0, 0, 0);
    height: 30px;
    
    transform: translate(-100vw, 0) ;
    animation: scroll 18s infinite linear; 
}
.stock_line_container div {
  margin: 0 5px;
    display: flex;
}

    
.stock_line{
    display: flex;
}
.corrent_symbol{
    
    margin: 0px 8px;
    display: flex;
}

.line_price{
    margin-left: 5px;
}

@keyframes scroll {
    0% {
        left: 100%;
        transform: translate(0, 0);
    }
    100% {
        left:0%;
        transform: translate(-100%, 0)
    }
}